/* border-box for everything */
* {
    box-sizing: border-box;
}

/* Setting CSS color variables and root font size */
:root {
    font-size: 16px;

    --text-secondary: #002626;
    --text-primary: #FEFEFE;
    --banner-back: #0E4749;
    --accent: #D3F6DB;
    --dark-green: #277D0F;
    --light-green: #477E19;
}
body {
    font-family: 'Archivo Narrow', Helvetica, sans-serif;
    color: var(--text-primary);
}

h1 {
    font-family: 'Archivo Black', Helvetica, sans-serif;
    font-size: 4rem;
}

h3 {
    font-size: 2.5rem;
}

h4 {
    font-size: 2rem;
    background-color: var(--text-primary);
    color: var(--text-secondary);
    padding: 0.5rem;
    border-radius: 5px;
}

button {
    font-family: 'Archivo Black', Helvetica, sans-serif;
    font-size: 1rem;
}

label, input {
    font-size: 1.5rem;
}

footer p {
    color: var(--accent);
}


.btn-primary {
    padding: 1rem;
    border-radius: 15px;
    border: none;
}

.btn-primary:hover {
    background-color: var(--accent);
    cursor: pointer;
}

.icon-link {
    text-decoration: none;
    color: var(--accent);
    font-size: 1.5rem;
}

.icon-link:hover {
    color: var(--text-primary)
}
.main-header {
    text-align: center;
    padding: 1rem;
    background-color: var(--banner-back);
    border-radius: 5px;
}

.main-header h1 {
    margin-bottom: 0.5rem;
}

.main-header p {
    padding: 0.25rem;
}

.accent-color {
    color: var(--accent);
}

.main-footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0.75rem;
    background-color: var(--banner-back);
}

.main-footer a {
    margin: 0 0.5rem;
}
.pitch-container-background {
    background: url(/come-on-you-team/static/media/pitch.9f55767a.png); 
    background-size: cover;
    height: 0;
    padding-top: calc(812 / 1280 * 100%);
    position: relative;
}

.pitch-container-inside {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.defense-container, .offense-container, .goal-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;  
}

.pitch-header {
    text-align: center;
    padding: 1rem;
}
.player-name-label {
    padding: 1rem;
}

.player-name-input {
    padding: 0.25rem;
    border-radius: 5px;
    border: 1px solid black;
}

.player-entry-form {
    padding: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.team-info {
    display: flex;
    justify-content: space-around;
    background-color: var(--banner-back);
    padding: 0.5rem;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}
body {
    background-color: var(--text-secondary);
}

.main-app {
    max-width: 1000px;
    margin: 0 auto;
}
